import { NextRequest, NextResponse } from "next/server";
import formidable from "formidable";
import fs from "fs";
import path from "path";

export const config = {
  api: {
    bodyParser: false, // we handle the file manually
  },
};

export async function POST(req: NextRequest) {
  const form = new formidable.IncomingForm();

  return new Promise<NextResponse>((resolve) => {
    form.parse(req as any, (err, fields, files: any) => {
      if (err) {
        resolve(NextResponse.json({ message: "Error parsing form" }, { status: 500 }));
        return;
      }

      const division = fields.division;
      const week = fields.week;
      const team1 = fields.team1;
      const team2 = fields.team2;
      const file = files.file;

      if (!division || !week || !team1 || !team2 || !file) {
        resolve(NextResponse.json({ message: "Missing fields or file" }, { status: 400 }));
        return;
      }

      const dir = path.join(process.cwd(), `public/d${division}/w${week}`);
      fs.mkdirSync(dir, { recursive: true });

      const filePath1 = path.join(dir, `${team1}vs${team2}.html`);
      const filePath2 = path.join(dir, `${team2}vs${team1}.html`);

      if (fs.existsSync(filePath1) || fs.existsSync(filePath2)) {
        resolve(NextResponse.json({ message: "Replay already exists. Overwrite manually." }, { status: 400 }));
        return;
      }

      fs.copyFileSync(file.filepath, filePath1);

      resolve(NextResponse.json({ message: "Replay uploaded successfully!" }));
    });
  });
}
